@import "../../../vars.scss";

.photo-block {
    margin: 50px auto;
    z-index: 5;
    position: relative;

    &__photo {
        height: 300px;
        width: 300px;
        object-fit: cover;

        &--thumbnail {
            cursor: pointer;
            width: 100%;
        }

        &--mobile {
            width: 100%;
            height: 300px;
            object-fit: cover;
        }
    }

    &--desktop {
        display: none;

        @media (min-width: 940px) {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            grid-gap: 20px 20px;
            width: 940px;
            margin: auto;
        }
    }

    &--mobile {
        position: relative;
        z-index: 5;

        @media (min-width: 940px) {
            display: none;
        }
    }

    &__slides {
        display: none;
        width: 100%;
    }

    &__thumbnail {
        &__row {
            display: flex;
            justify-content: center;
        }

        &__column {
            float: left;
            width: 33%;

            img {
                height: 35px;
                object-fit: cover;
            }
        }

        .active,
        .thumbnail:hover {
            opacity: 1;
        } 

        .thumbnail {
            opacity: 0.6;
            width: 100%;
        }  
    }

    &__prev-btn,
    &__next-btn {
        cursor: pointer;
        position: absolute;
        top: 40%;
        width: auto;
        padding: 16px;
        margin-top: -50px;
        color: white;
        font-weight: bold;
        font-size: 20px;
        border-radius: 0 3px 3px 0;
        user-select: none;
        -webkit-user-select: none;
        background: none;
        border: none;
    }

    &__next-btn {
        right: 0;
        border-radius: 3px 0 0 3px;
    }

    &__prev-btn {
        left: 0;
    }

    &__prev-btn:hover,
    &__prev-btn:focus,
    &__next-btn:hover,
    &__next-btn:focus {
        background-color: rgba(0, 0, 0, 0.8);
    }
}

