@import "../../vars.scss";

.story {
    margin: 0 auto;
    max-width: 700px;

    &__heading {
        font-size: 50px;
        margin: 0 0 60px;

    }

    &__subheading {
        font-size: 40px !important;
        margin: 60px 0 40px !important;
    }

    &__text {
        font-family: $font-secondary;
        letter-spacing: 1.25px;
        font-size: 16px;
        line-height: 25px;
        margin: 20px auto;
        text-align: left;

        @media (min-width: 600px) {
            max-width: 700px;
        }
    }

    &__photo {
        width: 50%;
        margin: 10px auto;
    }
}

.timeline {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    margin-bottom: 50px;

    @media (min-width: 600px) {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-content: center;
    }
   
    img {
        width: 100%;

        @media (max-width: 600px) {
            grid-area: 1 / 1 / 2 / 2;
            margin-bottom: 10px;
        }
        
        @media (min-width: 600px) {
            max-width: 300px;
        }
    }

    p {
        box-sizing: border-box;
        width: 100%;
        
        border-top: double 6px $color-primary;
        border-bottom: double 6px $color-primary;
        border-radius: 10%;
        padding: 30px;
        font-size: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: $font-secondary;

        @media (max-width: 600px) {
            grid-area: 2 / 1 / 3 / 2;
        }
        
        @media (min-width: 600px) {
            max-width: 350px;
        }
    }
}